
/*eslint-disable */
import Bus from './bus'
import * as Common from './common'
import CommonMenu from './components/common/menu'
import Loading from 'vux/src/components/loading/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

export default {
name: 'App',
components: {
Loading,
ViewBox,
Toast,
XDialog,
CommonMenu
},
data () {
return {
loading_data: {
show: false,
text: ''
},
toast_data: {
show: false,
type: 'text',
text: ''
},
common_menu: false,
common_menu_select: 'member',
dialog_show: false,
dialog_code: ''
}
},
created: function () {



Bus.$on('loading_data', data => {
this.loading_data = data
})

Bus.$on('toast_data', data => {
this.toast_data = data
})

Bus.$on('common_menu', data => {
this.common_menu = data
})

Bus.$on('common_menu_select', data => {
this.common_menu_select = data
})
},
methods: {
checkLogin: function () {
var open_id = Common.getOpenId()
if (open_id) {

this.getuserinfoop(open_id)
} else {
var url = window.location.href
if (url.indexOf('code=') != -1) {
var code = url.split('code=')[1]
code = code.split('&')[0]
if (Common.getWxCode() != code) {
window.localStorage.setItem('wx_code', code)
var data = {}
data.code = code

this.loading_data.show = true
this.$http.post(Common.getAjaxUrl() + '?s=WeiXin.GetToken', data).then(function (response) {
this.loading_data.show = false
var data = response.data.data
if(data.msg == '' && data.res){
window.localStorage.setItem('open_id', data.res.openid)
window.localStorage.setItem('wx_user_info',JSON.stringify(data.res))
this.getuserinfoop(data.res.openid)
}else{
this.toast_data.text = data.msg
this.toast_data.show = true
}
})
}
} else {
window.location.href = Common.getAuthUrl()
}
}
},
getuserinfoop(open_id){
this.loading_data.show = true
var data = {}
data.open_id = open_id
this.$http.post(Common.getAjaxUrl() + '?s=WeiXin.GetUserInfo', data).then(function (response) {
this.loading_data.show = false
var data = response.data.data
if (data.res) {
if(data.res.status == 0){
Common.setToast('text', '')
Common.setLoading(true, '')
return
}
window.localStorage.setItem('open_id', data.res.openid)
window.localStorage.setItem('member_id', data.res.id)
window.localStorage.setItem('user_info', JSON.stringify(data.res))
if (this.$route.path == '/login') {
this.$router.push({name: 'info'})
}
} else {
this.$router.push({name: 'login'})
}
})
},
}
}



