
/*eslint-disable */
import Bus from '../bus'
import * as Common from '../common'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

export default {
name: 'login',
components: {
XHeader,
Group,
XInput,
XButton,
CheckIcon,
Popup
},
data () {
return {
form: {
phone: '',
code: '',
read: false,
read2: false,
read3: false
},
send_text: '',
disabled_send: true,
disabled_save: true,
}
},
watch: {
form: {
handler: function () {
this.checkBtn()
},
deep: true
}
},
methods: {
checkBtn: function () {

if (this.form.phone == '' || !this.$refs.phone.valid) {
this.disabled_send = true
} else if (this.send_text == '') {
this.disabled_send = false
}

if (this.form.phone != '' && this.$refs.phone.valid && this.form.code != '' && this.form.read && this.form.read2 && this.form.read3) {
this.disabled_save = false
} else {
this.disabled_save = true
}
},
sendCode: function () {

this.waitSend()

Common.setLoading(true, '')
var data = {}
data.phone = this.form.phone
data.type = 1
this.$http.post(Common.getAjaxUrl() + '?s=WeiXin.SendCode', data).then(function (response) {
Common.setLoading(false, '')
var data = response.data.data
if (data.msg == '') {
Common.setToast('success', '')
} else {
Common.setToast('text', data.msg)
}
})
},
waitSend: function () {
var that = this
var time = 30
that.disabled_send = true
that.send_text = '(' + time + ')'
var interval = setInterval(function () {
time--
if (time > 0) {
that.send_text = '(' + time + ')'
} else {
that.send_text = ''
if (that.$refs.phone.valid) {
that.disabled_send = false
}
clearInterval(interval)
return false
}
}, 1000)
},
saveInfo: function () {
Common.setLoading(true, '')
var parmas = {}
parmas.phone = this.form.phone
parmas.code = this.form.code
parmas.openid = Common.getWxUserInfo().openid
parmas.nickname = Common.getWxUserInfo().nickname
parmas.headimgurl = Common.getWxUserInfo().headimgurl
this.$http.post(Common.getAjaxUrl() + '?s=WeiXin.SaveUserInfo', parmas).then(function (response) {
Common.setLoading(false, '')
var data = response.data.data
if (data.msg == '') {
window.localStorage.setItem('member_id', data.res.id)
window.localStorage.setItem('user_info', JSON.stringify(data.res))
this.$router.push({name:'info'});
} else {
Common.setToast('text', data.msg)
}
})
},
inputBlur: function () {
setTimeout(function () {
window.scrollTo(0, 0)
}, 100)
}
}
}



