/*eslint-disable */
import PDF from 'pdfjs-dist'
import Bus from '../../bus'
import * as Common from '../../common'
PDF.disableWorker = true;

export default {
    name: 'pdf',
    data() {
        return{
            width: 100,
            pages: 0,
            pdfDoc: ''
        }
    },
    props: ['pdfUrl'],
    methods: {
        getScroll(event) {
            // scrollBottom
            let scrollBottom =
                event.target.scrollHeight -
                event.target.scrollTop -
                event.target.clientHeight;
            if (scrollBottom < 100) {
                Bus.$emit('save_look', {})
            }
        },
        /*PDF*/
        loadFile: function(url){
            Common.setLoading(true, '')
            let _this = this;
            PDF.getDocument(url).then(function(pdf){
                _this.pdfDoc = pdf;
                _this.pages = _this.pdfDoc.numPages;
                _this.$nextTick(() => {
                    _this.renderPage(1);
                    Common.setLoading(false, '')
                    Bus.$emit('show_pdf', {})
                    var canheight = document.body.clientHeight
                    document.getElementById("div1").style.height = canheight -200 + 'px';
                });
            });  
        },
        /*PDF*/
        renderPage:function(num){
            let _this = this;
            this.pdfDoc.getPage(num).then(function(page){
                let canvas = document.getElementById('canvas'+num);
                let ctx = canvas.getContext('2d');
                let dpr = window.devicePixelRatio || 1;//
                let bsr = ctx.webkitBackingStorePixelRatio || ctx.mozBackingStorePixelRatio ||
                            ctx.msBackingStorePixelRatio || ctx.oBackingStorePixelRatio || ctx.backingStorePixelRatio || 1;//canvas,img
                let ratio = dpr / bsr;
                var viewport = page.getViewport(screen.availWidth / page.getViewport(1).width); //
                canvas.width = viewport.width * ratio;//,width:300px,height:150px
                canvas.height = viewport.height * ratio;
                canvas.style.width = viewport.width + 'px';//
                canvas.style.height = viewport.height + 'px';
                ctx.setTransform(ratio, 0, 0, ratio, 0, 0);
                var renderContext = {
                    canvasContext: ctx,
                    viewport: viewport
                };
                page.render(renderContext)
                    if (_this.pages > num) {
                        _this.renderPage(num + 1)
                    }
                });
        },
        /*PDF*/
        iconAdd: function(){
            let pageNum = document.querySelectorAll('canvas').length;
            this.width += 20;
            for(var i=0; i<pageNum; i++){
            document.querySelectorAll('canvas')[i].style.width = this.width + '%';
            }
            
        },
        /*PDF*/
        iconJian: function(){
            if (this.width == 100) return;
            let pageNum = document.querySelectorAll('canvas').length;
            this.width -= 20;
            for(var i=0; i<pageNum; i++){
            document.querySelectorAll('canvas')[i].style.width = this.width + '%';
            }
        }
    },
    created: function(){
        this.loadFile(this.pdfUrl);
    }
}



