
/*eslint-disable */
import Bus from '../../bus'
import * as Common from '../../common'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'member_score',
components: {
XHeader,
Tab,
TabItem,
XButton
},
data () {
return {
member_info: {},
score_info: {},
selected_cate_id:'',
datatype:[],
info:[],
}
},
created: function () {
this.member_info = Common.getUserInfo()

this.materialClass()

this.usermaterial('')
},
methods: {
goninfo(){
this.$router.push({name:'info'});
},
goExamination(info){
Common.setLoading(true, '')
var data = {}
data.id = info.id
data.user_id = this.member_info.id
this.$http.post(Common.getAjaxUrl() + '?s=WeiXin.creatquestions', data).then(function (response) {
Common.setLoading(false, '')
var data = response.data.data
if(data.msg !=''){
Common.setToast('text', data.msg)
}else{
var params = {};
params.id = data.res
params.class_id = info.id
this.$router.push({name:'exam',query: params})
}
})
},
gostudy(info){
var params = {};
params.id = info.id
this.$router.push({name:'study_msg',query: params});
},
handlertab(type){
this.usermaterial(type)
},
usermaterial(class_id){
this.info = []
Common.setLoading(true, '')
var data = {}
data.class_id = class_id
data.user_id = Common.getMemberId()
this.$http.post(Common.getAjaxUrl() + '?s=WeiXin.UserMateriaList', data).then(function (response) {
Common.setLoading(false, '')
var data = response.data.data
if(data.res){
this.info = data.res
}
})
},
materialClass: function () {
this.$http.get(Common.getAjaxUrl() + '?s=WeiXin.MaterialClassList').then(function (response) {
var data = response.data.data
if(data.res){
this.datatype = data.res
}
})
},
}
}



