
/*eslint-disable */
import Bus from '../../bus'
import * as Common from '../../common'
import pdf from './pdf'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'member_score',
components: {
XHeader,
XButton,
'v-pdf': pdf
},
data () {
return {
member_info: {},
info:{},
id:'',
second:0,
videourl:'',
gameover:true,
times:0,
videorue:false,
pdftrue:false,
showpdf:false,
savelook:false,
interval:0,
}
},
created: function () {
this.id= this.$route.query.id
this.member_info = Common.getUserInfo()

this.materialClass()
clearInterval(this.interval)
Bus.$on('show_pdf', data => {
this.showpdf = true
this.settimelast()
})
Bus.$on('save_look', data => {
this.savelook = true
})

},
methods: {
settimelast(){
var that = this
this.interval = setInterval(function () {
that.times ++
that.second = that.info.need_duration - that.times
var last = parseInt(that.info.need_duration - that.times)
if(last < 0){
last = 0
}
that.second = last
if(that.times >= that.info.need_duration && that.savelook){
clearInterval(that.interval)
that.savestudy()
}
}, 1000)
},
blackstudy(){

clearInterval(this.interval)

Bus.$off('show_pdf')
this.$router.push({name:'study'});
},
goExamination(){
Common.setLoading(true, '')
var data = {}
data.id = this.id
data.user_id = this.member_info.id
this.$http.post(Common.getAjaxUrl() + '?s=WeiXin.creatquestions', data).then(function (response) {
Common.setLoading(false, '')
var data = response.data.data
if(data.msg !=''){
Common.setToast('text', data.msg)
}else{
var params = {};
params.id = data.res
params.class_id = this.id
this.$router.push({name:'exam',query: params})
}
})
},
materialClass: function () {
var data = {}
data.id = this.id
Common.setLoading(true, '')
this.$http.post(Common.getAjaxUrl() + '?s=WeiXin.MaterialClassMsg', data).then(function (response) {
var data = response.data.data
Common.setLoading(false, '')
if(data.res){
this.info = data.res
this.second = data.res.need_duration
this.videourl = 'http://qncdn.allergan.eoiyun.com/' + data.res.file_name


if(data.res.type == 2 || data.res.type == 3){
this.videorue = true
var that = this
setTimeout(function() {
var vid = document.getElementById("videoLive")
var last = 0;

vid.ontimeupdate = function () {
var current = vid.currentTime;
if(current - last > 2) {
vid.currentTime = last;
} else {
last = current;
}
}

vid.addEventListener("timeupdate",function(){
var curTime = vid.currentTime;
if(curTime >= that.info.need_duration && that.gameover){

that.gameover = false
that.savestudy()
}
that.times = curTime
var last = parseInt(that.info.need_duration - curTime)
if(last < 0){
last = 0
}
that.second = last
})
},100);
}

if(data.res.type == 1){
this.pdftrue = true
}
}
})
},
savestudy(){
var data = {}
data.id = this.id
data.user_id = this.member_info.id
data.duration = this.times
data.points_get = this.info.get_score
this.$http.post(Common.getAjaxUrl() + '?s=WeiXin.savestudy', data).then(function (response) {
var data = response.data.data
if(data.res){
Common.setToast('text', '')
}
})
},
}
}



