/*eslint-disable */
import Bus from './bus'

export function getBaseUrl() {
    if (process.env.NODE_ENV == 'production') {
        return window.location.protocol + '//' + document.domain
    } else {
        return window.location.protocol + '//' + document.domain + ':9008'
    }
}

export function getAjaxUrl() {
    return window.location.protocol + '//' + document.domain + '/api/public/'
}

export function getWxInfo() {
    return JSON.parse(window.localStorage.getItem('wx_info'))
}

export function getWxCode() {
    return window.localStorage.getItem('wx_code')
}

export function getWxUserInfo() {
    return JSON.parse(window.localStorage.getItem('wx_user_info'))
}

export function getAuthUrl() {
    var call_back = encodeURIComponent(getBaseUrl() + '/app/#/')
    return 'https://open.weixin.qq.com/connect/oauth2/authorize?appid='
        + process.env.APPID + '&redirect_uri='
        + call_back + '&response_type=code&scope=snsapi_userinfo&state=1#wechat_redirect'
}

export function getUid() {
    if (location.host == 'eosiscrm.eoiyun.com') {
        return 18
    }
    if (process.env.NODE_ENV == 'production') {
        return 28
    } else {
        return 18
    }
    // return checkWeiXin() ? 28 : 18;
}

export function getOpenId() {
    if (process.env.NODE_ENV == 'production') {
        return window.localStorage.getItem('open_id')
    } else {
        return 'oPzHCuDdTLgavzdgLwlY2oy6EvyU'
    }
    // return checkWeiXin() ? window.localStorage.getItem('open_id') : 'o2_qyjjRaC7rIvD2CTq_TRXcOAfY';
}

export function getMemberId() {
    return window.localStorage.getItem('member_id')
}

export function getUserInfo() {
    return JSON.parse(window.localStorage.getItem('user_info'))
}

export function checkWeiXin() {
    return window.navigator.userAgent.toLowerCase().match(/MicroMessenger/i) == 'micromessenger' ? true : false
}

export function setLoading(show, text) {
    Bus.$emit('loading_data', {
        show: show,
        text: text
    });
}

export function setToast(type, text) {
    Bus.$emit('toast_data', {
        show: true,
        type: type,
        text: text
    });
}

export function setCommonMenu(data) {
    Bus.$emit('common_menu', data);
}

export function setMenuSelect(data) {
    Bus.$emit('common_menu_select', data);
}


