import Login from './components/login.vue'
import MemberInfo from './components/user/info.vue'
import MemberScore from './components/user/score.vue'
import MemberAchievement from './components/user/achievement.vue'
import MemberStudy from './components/user/study.vue'
import MemberStudyMsg from './components/user/study_msg.vue'
import Exam from './components/user/exam.vue'
import FormalExam from './components/user/formalexam.vue'
// 
let Routes = [
  { name: 'login', path: '/login', component: Login },
  { name: '', path: '/', component: MemberInfo },
  { name: 'info', path: '/info', component: MemberInfo },
  { name: 'score', path: '/score', component: MemberScore },
  { name: 'achievement', path: '/achievement', component: MemberAchievement },
  { name: 'study', path: '/study', component: MemberStudy },
  { name: 'study_msg', path: '/study_msg', component: MemberStudyMsg },
  { name: 'exam', path: '/exam', component: Exam },
  { name: 'formalexam', path: '/formalexam', component: FormalExam }
]

export { Routes }



