
/*eslint-disable */
import Bus from '../../bus'
import * as Common from '../../common'
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

export default {
name: 'member_score',
components: {
XHeader,
XTable,
LoadMore,
Alert
},
data () {
return {
member_info: {},
score_info: {},
show:false,
}
},
created: function () {
this.member_info = Common.getUserInfo()

this.memberInfo()
},
methods: {
showalert(){
this.show = true
},
memberInfo: function () {
Common.setLoading(true, '')
var data = {}
data.user_id = Common.getMemberId()
this.$http.post(Common.getAjaxUrl() + '?s=WeiXin.UserStudyInfo', data).then(function (response) {
Common.setLoading(false, '')
var data = response.data.data
if(data.res){
this.score_info = data.res
}
})
},
}
}



