
/*eslint-disable */
import Bus from '../../bus'
import * as Common from '../../common'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import { Button, PasswordInput, NumberKeyboard} from 'vant'
export default {
name: 'member_info',
components: {
XHeader,
Group,
Grid,
GridItem,
Popup,
Badge,
[Button.name]: Button,
[PasswordInput.name]: PasswordInput,
[NumberKeyboard.name]: NumberKeyboard
},
data () {
return {
member_info: {},
is_show:false,
showKeyboard:true,
value:'',
}
},
created: function () {


},
methods: {
onInput(key) {
this.value = (this.value + key).slice(0, 6);
},
onDelete() {
this.value = this.value.slice(0, this.value.length - 1);
}
}
}



