
/*eslint-disable */
import Bus from '../../bus'
import * as Common from '../../common'
import XHeader from 'vux/src/components/x-header/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

export default {
name: 'member_score',
components: {
XHeader,
Radio,
Group,
XButton,
Checklist,
Confirm
},
data () {
return {
member_info: {},
question:[],
questioninfo:[],
id:'',
num:0,
radios:[],
titles:'',
radiovalue:'',
checkvalue:[],
type:'',
last_answer:'',
button_show:true,
text:'',
show:false,
kaomsg:false,
kaojs:false,
finish:false,
}
},
created: function () {
this.id= this.$route.query.id
this.member_info = Common.getUserInfo()
this.memberInfo()
},
methods: {
onConfirm () {
this.$router.push({name:'info'});
},
gamestart(){
this.kaomsg = true
this.kaojs = false
},
gameover(){
if(this.radiovalue === ''){
Common.setToast('text', "")
return
}
this.last_answer = this.last_answer + ',' + this.radiovalue
Common.setLoading(true, '')
var data = {}
data.id = this.id
data.answer = this.last_answer
this.$http.post(Common.getAjaxUrl() + '?s=WeiXin.Calculation', data).then(function (response) {
Common.setLoading(false, '')
var data = response.data.data
if(data.msg != ''){
Common.setToast('text', data.msg)
}else{
this.show = true
this.text = "" + data.res + ""
}
})
},
nextqu(){
if(this.radiovalue === ''){
Common.setToast('text', "")
return
}
this.last_answer = this.last_answer + ',' + this.radiovalue
this.showquestion(this.num)
},
changecheck(val, label) {
this.radiovalue = val.join("/")
},
change (value, label) {
this.radiovalue = value + 1
},
showquestion(index){
this.radiovalue = ''
this.checkvalue = []
var newarray = []
var data = this.questioninfo[index].answers.split(/[(\r\n)\r\n]+/)
for (let i = 0; i < data.length; i++) {
const element = data[i];
var obj = {}
obj.key = i
obj.value = data[i]
newarray[newarray.length] = obj
}
this.type = this.questioninfo[index].type
this.radios = newarray
this.num = this.num + 1
this.titles = this.num + '' + this.questioninfo[index].title
+ '(' + this.questioninfo[index].label_a + ') '
+ '(' + this.questioninfo[index].type_a + ')'
if(this.num >= this.questioninfo.length){
this.button_show = false
}
},
memberInfo: function () {
Common.setLoading(true, '')
var data = {}
data.id = this.id
data.user_id = Common.getMemberId()
this.$http.post(Common.getAjaxUrl() + '?s=WeiXin.SelectTestInfo', data).then(function (response) {
Common.setLoading(false, '')
var data = response.data.data
if(data.res.score != -1){
this.finish = true
}
if(data.res){
this.kaojs =true
this.question = data.res
this.questioninfo = data.res.questions
this.showquestion(0)
}else{
Common.setToast('text', data.msg)
return
}
})
},
}
}



